.create{
    display: flex;
    flex-direction: row;
}
.create-container{
    display: flex;
    justify-content: space-evenly;
    margin-left: 12vw;
    height: 100vh;
    width: 100vw;
} 
.name-container{
    margin-top: 30vh;
   
}
.layerOrder-container{
    display: flex;
    flex-direction: column;
    width: 18vw;
    height: 30vh;
    background-color: #27292C;
    border-radius: 10px 10px 0px 0px ;
}
#subhed{
    display: flex;
    justify-content: space-around;
    height: 5vh;
}
.layers{
    display: flex;
    flex-direction: column;
    /* background-color: #27292C; */
    height: 90vh;
    width: 40vw;
    margin-top:6vh ;
    overflow-y: scroll;
    overflow-x: hidden;
    margin-right: 8px;
}
#layer-ttl{
    border-bottom:  2px solid white;
}
#arrow{
    background-color: white;
    width: 1px;
    height: 20px;
    margin:10px
}
.map-lay{
    display: flex;
    height:6vh;
    align-items: center;
}

.nft-container{
    margin-top: 7vh ; 
    display: flex;
    flex-direction: column;
    align-items:center ; 
    justify-content: space-between;
 
}
.nft{
    display: flex;
    justify-content: center;
    width: 22vw;
    height:60vh;
    margin-top: 6vh;
}

#nft-png1{
   /* position: absolute; */
    width:388px;
    height:380px;
    border-radius: 10px;
}
#nft-png2{
    width: 200px;
    position: absolute;
    right: 9vw;
    top: 16vh;
    z-index: 1;
}
#nft-png3{
    width: 150px;
   position: absolute;
   right: 10.5vw;
   top: 28vh;

}
.map{
    display: flex;
    width: 100%;
    height:300px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.final-img{
    display: flex;
    justify-content: center;
   height: 100vh;
}

::-webkit-scrollbar {
    width: 30px;
   
}
.background-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color:#27292C ;
    border-radius: 10px;
    margin: 10px;
    width: 200px;
    height: 200px;
    
}
.head-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color:#27292C ;
    border-radius: 10px;
    margin: 10px;
    width: 200px;
    height: 200px;
}
.outfit-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color:#27292C ;
    border-radius: 10px;
    margin: 10px;
    width: 200px;
    height: 200px;
}
.background-img{
    width: 200px;
    height: 100px;
}
.head-img{
    width: 100px;
}
#p-{
    width: 200px;
}
input{
    color: white;
}
p{
    margin: 0;
}
.download-btn{
    background-color:black;
    border: white 3px solid;
   width: 22vw;
   height: 6vh;
}
.mint{
    background-color:black;
    border: white 3px solid;
   width: 22vw;
   height: 6vh;  
   margin-right: 0;
   margin-top: 2vh;;
}
h3{
    margin: 0;
    font-size: 20px;
}
.mnt-comb{
    display: flex;
    justify-content: space-around;
    width: 28vw;
    margin-top: 2vh;
}
.mnt-con{
    background-color: #27292C ;
    width: 10vw;
    height: 6vh;;
}
input{
    background-color: #444444 ;
    border:solid 1px transparent;
    border-radius: 10px;
    font-family: 'Poppins', sans-serif;
}
button{
    background-color: #00CC9B ;
    border: none;
    color: white;
    width: 39vw;
    height: 3vh;
    text-align: center;
}
a{
    text-decoration: none;
}
                    
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    background-color: #27292C ;
    border-radius: 10px;

}
                    
::-webkit-scrollbar-thumb {
    background-color: #00CC9B ;
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
@media(max-width:1150px){
    .create-container{
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        margin: 0;
    }
    .name-container{
        margin: 0;
    }
    .layers{
        margin: 0;
       height: 60vh;
        width: 80vw;
        margin-top: 30px;
     
    }
    #nft-png1{
        position: absolute;
        z-index: -1;
    }
    .nft{
        height: 20px;
    }
    .nft-container{
        margin: 20px;
       display: flex;
       height: 2px
    }
    .mint-dwnld{
        width: 80vw;
        /* position: fixed; */
    }
    .mnt-con{
        width: 60vw;
    }
    .download-btn{
        width: 75vw;
    }
    .mint{
        width: 75vw;
    }
    .map{
        width: 100%;
        height:150px; 
        
    }
    .name-container{
        margin-top: 20px;
    }
    .head-container{
        width: 200px;
        height: 120px;
    }
    .background-container{
        width: 200px;
        height: 120px;
    }
    .outfit-container{
        width: 200px;
        height: 120px;
    }
    .layerOrder-container{
        width: 60vw;
        margin-top: 100px;
        height: 30vh;
    }
    .background-img{
        width:200px;
        height: 70px;
    }
    #nft-png2{
       position: relative;
       top:90px;
       right:25px
    
    }
    #nft-png3{
        position: static;
        z-index: 100;
        margin-bottom: 50px;
       
     }
     .final-img{
         align-items: center;
     }
    

}
@media(max-width:630px){}